html {
    width:100%;
    height: 100%;
    display: table;
}

body {
    width:100%;
    display:table-cell;
}

html, body {
    margin: 0px;
    padding: 0px;
}

.test {
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.test1 {
    display:flex;
    align-items: flex-start;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 15%;
    font-family: "Questrial", sans-serif;
}

h1,h2 {
  font-family: "Saira", sans-serif;
}

p,label {
    font-family: "Questrial", sans-serif;
    font-size: 1.2rem;
  }
  
button {
    margin: 5px 4px;
    padding: 5px;
}
.post-button {
    background-color:#1475fb;
    color: white;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    font-weight: bold;
    font-size: 1rem;
    font-weight: 700;
    border-radius: 15px;
    padding: 7px 10px;
    margin: auto 1px;
    min-width: 85px;
} 
.post-button:hover {
    background-color:#1258b9;
    color: white;
    cursor: pointer;
} 

.button-edit {
    color:#21ba45;
}
.button-edit:hover {
    color:#006f00;
    cursor: pointer;
}
.button-delete{
    color: #af0000;
}
.button-delete:hover{
    color:#840000;
    cursor: pointer;
}
label,input {
    display: block;
    box-sizing: border-box;
}

.cover_page {
    height: 100%;
}

#main_background {
    background-image:url("./images/background.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;

}

#main_container {
    box-sizing: border-box;
    margin: 10px;
    padding: 30px 10%;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 1px 1px 12px 1px rgba(0, 0, 0, 0.7);
}

.main_button {
    background-color: #051b64;
    color: #ffffff;
    font-family: "Questrial", sans-serif;
    border-radius: 25px;
    margin: 10px;
    padding: 8px;
    min-width: 90px;
    
}
.main_button:hover {
    background-color: #0249ab;
}
.header-custom {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-basis: 100%;
    margin-bottom: 10px;
    margin-top: 10px;
}

.nav-item {
    margin: 0 5px;
    padding: 3px 5px;
    text-decoration: none;
    text-transform: capitalize;
    font-weight: bold;
}

.nav-item:hover {
    background-color: #c7c7c7;
}

.logout {
    background-color:#ce2323;
    color: white;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 20px;
    padding: 7px 7px;
    margin: auto 1px;
    min-width: 65px;
}

.logout:hover {
    background-color: #9e1c1c;
}

.posts_container {  
    display: flex;
    flex-direction: column-reverse;
    flex: 1 1 100%;
    margin: 10px 0;
}

.post {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 10px 0;
    padding: 10px;
    min-height: 55px;
    box-sizing: border-box;
    box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 1);
    background-color: white;
}

.post-text-container {
    margin: 2px 0;
    background-color: rgb(236,236,236);
    flex-basis: 100%;
    font-size: 1.1em;
}

.post-buttons-containter {
    margin-left: auto;
    margin-top: 5px;
}

.post-username-container {
    font-weight: bold;
    text-transform: capitalize;
    color: blue;
    /* min-height: 33px; */
    box-sizing: border-box;
    /* padding-top:8px; */
}

.post-date-container {
    /* padding-top:8px; */
    font-style: italic;
    margin-left: 5px;
    color: #616770;
    font-size: 12px;
}

.input-post {
    width: 80%;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 1);
    border-radius: 10px;
    resize: none;
    margin: 2px auto;
}

.input-post-container {
    flex: 1 1 100%;
    margin: 10px 0;
}

span:hover {
    color: blue;
    cursor: pointer;
}

.input-comment-container {
    display: flex;
    flex-wrap: wrap;
    flex-basis: 100%;
}
.input-comment {
    border: none;
    resize: none;
    width: 80%;
    border-bottom: 1px solid gray;
    margin-top: 10px;
}
.comment-form {
    flex-basis: 100%;
    display: flex;
    align-items: center;
    min-height: 1.5em;
}
.comment {
    flex-basis: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: 10px 0;
    padding: 10px;
    min-height: 55px;
    box-sizing: border-box;
    /* box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 1); */
    border: 1px solid black;
    background-color: lightblue;
}
.personal-container{
    display: flex;
    flex-wrap: wrap;
    border: 2px solid black;
    margin: 20px auto;
    /* min-height: 40vh; */
}
.image-container {
    margin: 0 auto;
    max-width: 400px;
}
.info-container {
    margin: auto ;
    padding: 10px;
    align-self:center;
}
.dropdown-icon {
    margin-top: 15px;
}
.info-text {
    font-weight:600;
    letter-spacing: 1px;
}